@import './../../vars.scss';

.gpt3__blog {
  display: flex;
  flex-direction: column;

  &-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;

    h1 {
      font-size: 62px;
      line-height: 75px;
      font-family: $font-family;
      font-weight: 800;
    }
  }

  &-container{
    display: flex;
    flex-direction: row;

    &_groupA{
        flex: 0.75;
        margin-right: 2rem;
    }

    &_groupB{
        flex: 1;

        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 2rem;
    }
  }
}

@media screen and (max-width: 990px) {
  .gpt3__blog-container {
    flex-direction: column-reverse;

    &_groupA{
        margin: 2rem 0;

        &_groupA &_acticle{
            width: 48%;
        }

        &_groupA &_article-image{
            height: 250px;
        }
    }
  }
}

@media screen and (max-width: 700px) {
  .gpt3__blog-container_groupB {
    grid-template-columns: repeat(1, 1fr);
  }

  .gpt3__blog-container_groupA .gpt3__blog-container_article {
    width: 100%;
  }

  .gpt3__blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__blog-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }
}
