@import './../../vars.scss';

.gpt3__possibility {
  display: flex;
  flex-direction: row;

  &-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;

    & img {
      width: 100%;
      height: 100%;
    }
  }

  &-content {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    flex-direction: column;

    & h4 {
      font-family: $font-family;
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: #71e5ff;

      &:last-child {
        color: #ff8a71;
      }
    }

    & h1 {
      font-family: $font-family;
      font-weight: 800;
      font-size: 34px;
      line-height: 45px;
      margin: 1rem 0;
    }

    & p {
      color: $color-text;
      font-family: $font-family;
      font-weight: 400;
      font-size: 16px;
      line-height: 30px;

      margin-bottom: 2rem;
    }
  }
}

@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;

    &-image{
        margin: 1rem 0;
    }

    &-content{
        margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 700px) {
  .gpt3__possibility-image img {
    width: 100%;
    height: 100%;
  }
}
