@import "./../../vars.scss";

.gpt3__blog-container_article {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: $color-footer;

  &-image {
    width: 100%;
    height: 100%;
    background: $color-bg;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;

    p {
      font-family: $font-family;
      font-size: 11.65px;
      font-weight: 700;
      line-height: 35px;

      color: #fff;

      &:last-child{
        cursor: pointer;
      }
    }

    h3 {
      font-family: $font-family;
      font-size: 25.11px;
      font-weight: 800;
      line-height: 30.3px;

      color: #fff;
      margin-bottom: 5rem;
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25p;
  }
}
